import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

interface SEOProps {
  children?: React.ReactNode;
  title?: string;
  description?: string;
}

const SEO = ({ children, title, description }: SEOProps) => {
  const { site } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
          description
          author
        }
      }
    }
  `);

  const metaDescription = description || site.siteMetadata.description;
  const metaTitle = title || site.siteMetadata?.title;

  return (
    <>
      <html lang="ko" />

      {/* Font */}
      <link rel="stylesheet" href="https://fonts.googleapis.com/css?family=Source+Code+Pro" />
      <link rel="stylesheet" href="https://fonts.googleapis.com/css?family=Quattrocento+Sans" />
      <link
        rel="stylesheet"
        href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"
      />

      {/* SEO */}
      <title id="title">{metaTitle}</title>

      <meta name="viewport" content="width=device-width, initial-scale=1" />
      <meta charSet="utf-8" />
      <meta name="robots" content="index,follow" />
      <meta name="googlebot" content="index,follow" />
      <meta name="color-scheme" content="light" />

      <meta id="description" name="description" content={metaDescription} />
      <meta id="og:title" property="og:title" content={metaTitle} />
      <meta id="og:description" property="og:description" content={metaDescription} />
      <meta id="og:type" property="og:type" content="website" />
      <meta id="og:site_name" property="og:site_name" content="kciter.so" />
      <meta id="og:locale" property="og:locale" content="ko_KR" />
      <meta id="twitter:card" name="twitter:card" content="summary" />
      <meta id="twitter:creator" name="twitter:creator" content="Sunhyoup Lee" />
      <meta id="twitter:title" name="twitter:title" content={metaTitle} />
      <meta id="twitter:description" name="twitter:description" content={metaDescription} />
      <meta id="og:image" property="og:image" content="https://kciter.so/images/og.png" />
      {children}
    </>
  );
};

export default SEO;
